@keyframes rotate-animation {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch__round {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
}

.stopwatch__centr {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch__sec {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  margin-bottom: 38vmin;
  animation: 60s linear infinite rotate-animation;
  position: absolute;
}

.stopwatch__min {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin-bottom: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-animation;
  position: absolute;
}

.stopwatch--speed-up .stopwatch_sec {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch_min {
  animation: 600s steps(60, end) infinite rotate-animation;
}

